import { remote,desktopCapturer } from 'electron'

const { id,size } = remote.screen.getPrimaryDisplay() //workAreaSize >= size,ÂâçËÄÖÂåÖÂê´Â±èÂπïÂ∑¶Âè≥ÁöÑÈªëËæπÊ°Ü

//Ëß£ÂÜ≥canvasÊ∏≤ÊüìÊ®°Á≥ä
 export const setCanvas = (canvas,width,height)=>{
  let ctx = canvas.getContext('2d')
  let backingStorePixelRatio = ctx.backingStorePixelRatio ||
  ctx.webkitBackingStorePixelRatio ||
  ctx.mozBackingStorePixelRatio ||
  ctx.msBackingStorePixelRatio ||
  ctx.oBackingStorePixelRatio ||
  ctx.backingStorePixelRatio || 1

  const pixelRatio = (window.devicePixelRatio || 1)/backingStorePixelRatio //Ëé∑ÂæóÂ±èÂπïÂÉèÁ¥†ÊØîÔºåËøõË°åÁº©ÊîæÔºåÂê¶ÂàôÁªòÂà∂‰ºöÂá∫Áé∞Ê®°Á≥ä
  canvas.width = width * pixelRatio //Áõ∏Â∫îÁº©ÊîæÁîªÂ∏É
  canvas.height = height * pixelRatio
  ctx.scale(pixelRatio,pixelRatio)

  canvas.style.width = width + 'px'//ÂÆûÈôÖÊåâÁÖßÂ±èÂπïÂ§ßÂ∞èÁªòÂà∂
  canvas.style.height = height +'px'

  return ctx
}


export const captureScreen = () => {
  if(process.platform==='win32'){ //ËÄÅÂùëÔºödesktopCapture=>linux‰∏ãÊó†Êïà
    desktopCapturer.getSources(
      { types: ['screen'],thumbnailSize:{width:0,height:0}}
    ).then(async sources => {
      for (let source of sources) {
        // console.log(typeof source.display_id) //Âùë
        // console.log(typeof id) //Âùë
        if (source.display_id === id.toString()) {
          const stream = await navigator.mediaDevices.getUserMedia({
            audio: false,
            video: {
              mandatory: {
                chromeMediaSource: 'desktop',
                chromeMediaSourceId: source.id,
                minWidth: size.width,
                maxWidth: size.width,
                minHeight: size.height,
                maxHeight: size.height
              }
            }
          })
          handleStream(stream)
        }
      }
    })

   } else { //linux
     navigator.mediaDevices.getUserMedia({
       audio: false,
       video: {
         mandatory: {
           chromeMediaSource: 'desktop',
           // chromeMediaSourceId: source.id, //Âá∫Áé∞NotReadableError,ÊòØÂõ†‰∏∫getPrimaryDisplay()ËøîÂõûÁöÑid‰∏ç‰∏ÄËá¥Ôºå‰∏çÂÅöÂ§öÂ±èÂπïÁõ¥Êé•ÂéªÊéâÂ∞±ÂèØ‰ª•‰∫Ü
           minWidth: size.width,
           maxWidth: size.width,
           minHeight: size.height,
           maxHeight: size.height,
         },
       }
     }).then( stream => handleStream(stream))
   }
}


