<template>
  <div id="layout">
    <div id="mask"
      @mousedown="onSelectRegion"
      :style="{cursor: completeSelectRegion? 'default':'crosshair'}">
    </div>

<!-- caputure region -->
    <div id="captureRegion"
      :style="captureRegionStyle"
      v-show="isCapture">
    </div>

<!-- ‰∏§‰∏™canvasÔºå‰∏Ä‰∏™‰∏ªÊòæÁ§∫Ôºå‰∏Ä‰∏™‰∏ªËæÖÂä© -->
      <!-- canvas: mouseupÊó∂ËÆæÁΩÆ -->
      <!-- :width="canvasWidth"
      :height="canvasHeight" -->
      <!-- style: mouseupÊó∂ËÆæÁΩÆ -->
      <!-- width:canvasWidth+'px',
      height:canvasHeight+'px', -->
    <canvas id='display-canvas'
      ref='display'
      v-show="completeSelectRegion"
      :style="{
        position:'absolute',
        left:canvasX+'px',
        top:canvasY+'px',
        zIndex:999,
        pointerEvents:'none'}"
      >
    </canvas>

    <!-- width/heightÔºöÂêå‰∏ä -->
    <canvas id='assist-canvas'
      ref='assist'
      v-show="completeSelectRegion"
      :style="{
        position:'absolute',
        left:canvasX+'px',
        top:canvasY+'px',
        zIndex:999,
        cursor:'move'}"
      @mousedown="onDrag">
    </canvas>


<!-- Â∑•ÂÖ∑Êù° -->
    <ToolBar
      @initSelect="initSelect()"
      @closeDrag="canDrag=false"
      v-position="{x,y,width,height}"
      v-show="completeSelectRegion"
      :toolbarBottom="toolbarBottom"
      :canvasProps="{canvasX,canvasY,canvasWidth,canvasHeight}"
      :clipDesktop="clipDesktop"
    />
<!-- È¢úËâ≤„ÄÅ‰ΩçÁΩÆÊèêÁ§∫Ê°Ü -->
    <ColorTip v-show="!isCapture" v-if="showColorTip"/>
<!-- Ê°åÈù¢ÊçïËé∑ -->
    <canvas id="desktop-canvas" ref='desktop' :style="{visibility: win32? 'hidden':'visible'}"></canvas>
    <video id="video"></video>
  </div>
</template>


<script>
import ToolBar from "./components/ToolBar";
import ColorTip from './components/ColorTip'
import { captureScreen,setCanvas} from "./utils/captureScreen";
import {ipcRenderer} from 'electron'

ipcRenderer.on('flushDesktopCapture',()=>captureScreen()) //F1

export default {
  name: "app",
  components: {
    ToolBar,
    ColorTip
  },
  data() {
    return {
      x: 0,
      y: 0,
      width: 0,
      height: 0,
      win32:process.platform==='win32',

      isCapture: false,
      completeSelectRegion: false,
      canDrag: true,

      captureRegionStyle: {
        position: "absolute",
        zIndex: 99,
        // cursor:'move',
        background:'rgba(0,0,0,0)',
        draggable: false,
        borderWidth:'2px',
        borderColor:this.$root.$data.captureColor,
        borderStyle:'solid',
        boxShadow: "0px 0px 2px 2px rgba(85, 80, 80, 0.3)",
        left: "0px", // =this.x
        top: "0px", // = this.y
        width: "0px", //this.width
        height: "0px" //this.height
      },

      showColorTip: this.$root.$data.showColorTip
    };
  },
  mounted(){
    captureScreen()
  },
  computed:{
    toolbarBottom(){ //‰º†ÁªôÂ≠êÁªÑ‰ª∂ÔºåÁªôcolorpickerÂÆö‰Ωç
      return  this.$root.$data.screenHeight - (this.y+this.height+60+40)
    },
    canvasX(){
      return this.x+parseInt(this.captureRegionStyle.borderWidth)
    },
    canvasY(){
      return this.y+parseInt(this.captureRegionStyle.borderWidth)
    },
    canvasWidth(){
      let width = this.width - 2*parseInt(this.captureRegionStyle.borderWidth)
      return width>0? width:0
    },
    canvasHeight(){
      let height = this.height - 2*parseInt(this.captureRegionStyle.borderWidth)
      return height>0? height:0
    }
  },
  methods: {
    clipDesktop(){
      let desktop = this.$refs.desktop
      const ctx = this.$refs.display.getContext('2d')
      ctx.clearRect(0,0,this.canvasWidth,this.canvasHeight)
      ctx.drawImage(
        desktop,
        this.canvasX,this.canvasY,this.canvasWidth,this.canvasHeight,
        0,0,this.canvasWidth,this.canvasHeight
      )
      ctx.globalCompositeOperation = "source-over"
    },
    initSelect() {
      //ÊØèÊ¨°ÈÄâÊã©Âå∫ÂüüÂâçÂàùÂßãÂåñÈÄâÂå∫Áä∂ÊÄÅ,‰πüÂ∞±ÊòØ"ÂèñÊ∂à"Â∑•ÂÖ∑ÊåâÈíÆ‰ΩúÁî®
      this.completeSelectRegion = false;
      this.isCapture = false;
      this.canDrag = true;
      this.captureRegionStyle.left = '0px';
      this.captureRegionStyle.top = '0px';
      this.captureRegionStyle.width = '0px';
      this.captureRegionStyle.height = '0px';
    },
    onSelectRegion(e) {
      //Èº†Ê†áÊåâ‰∏ãÂºÄÂßãÊà™Âõæ
      if (this.completeSelectRegion) {
        return false;
      } //ÈÄâÂå∫ÊàêÂäüÂêéÔºå‰∏çÂèØÂÜçÁÇπÂáªÔºåÈô§ÈùûÁÇπÂáª"ÂèñÊ∂à"ÈáçÊñ∞ÈÄâ

      // if(this.showPixelCanvas){
      //   return false
      // } //ÂÉèÁ¥†Êü•ÁúãÊó∂Ôºå‰∏çÂèØËøõË°åÊà™ÂõæÈÄâÂèñ

      this.initSelect();
      this.x = e.clientX;
      this.y = e.clientY;
      this.captureRegionStyle.left = this.x + "px";
      this.captureRegionStyle.top = this.y + "px";

      document.onmousemove = e => {

        const width = e.clientX - this.x; //ÂèØËÉΩÂèçÂêëÈÄâÂå∫,Â∞±‰∏∫Ë¥üÊï∞
        const height = e.clientY - this.y;

        // Âà§Êñ≠ÊòØÂê¶ÊòØÂú®Êà™ÂõæÔºåËÄå‰∏çÊòØÁ∫ØÁ≤πÁÇπÂáªÈº†Ê†áÁ≠âÊôÆÈÄöÊìç‰Ωú
        this.isCapture = width > 15; //width<0ÊòØÂèçÂêëÈÄâÂå∫Ôºå‰ΩÜÂèçÂêëÈÄâÂå∫ÈÅáÂà∞bugÔºåÂ∞öÊú™Ëß£ÂÜ≥
        if (this.isCapture) {
          //ÊòØÂú®Êà™ÂõæËøõË°åÈÄâÂå∫,Êõ¥Êñ∞ÂÆΩÈ´ò
          this.width = width;
          this.height = height;
          this.captureRegionStyle.width = this.width + "px";
          this.captureRegionStyle.height = this.height + "px";
        }
      }
      document.onmouseup = () => { //BUG:ÂÅ∂Â∞î‰∏¢Â§±mouseup‰∫ã‰ª∂ÔºåÊàëÈº†Ê†áÈóÆÈ¢òÂêóÔºü
        //ÊîæÂºÄÈº†Ê†áÔºåÊ∏ÖÈô§ÁßªÂä®‰∫ã‰ª∂ÔºåÁîüÊàêÈÄâÂå∫
        document.onmousemove = null;
        this.completeSelectRegion = this.canDrag = this.isCapture;

        //-------ËÆæÁΩÆÂÆΩÈ´ò„ÄÅÊ∏ÖÁ©∫ÁîªÂ∏ÉÈÉΩ‰ºö‰ΩøcanvasÁä∂ÊÄÅÊ∏ÖÁ©∫ÔºåÊâÄ‰ª•Âú®ËøôÈáåÂ§ÑÁêÜÈ´òdpiÊ®°Á≥äÈóÆÈ¢ò
        setCanvas(this.$refs.display,this.canvasWidth,this.canvasHeight)
        setCanvas(this.$refs.assist,this.canvasWidth,this.canvasHeight)
        //-------
        this.clipDesktop()
      }
    },
    onDrag(e) {
      if (this.completeSelectRegion && this.canDrag) {
        // Èº†Ê†áÁÇπÂáªÁâ©‰ΩìÈÇ£‰∏ÄÂàªÁõ∏ÂØπ‰∫éÁâ©‰ΩìÂ∑¶‰æßËæπÊ°ÜÁöÑË∑ùÁ¶ª=ÁÇπÂáªÊó∂ÁöÑ‰ΩçÁΩÆÁõ∏ÂØπ‰∫éÊµèËßàÂô®
        // ÊúÄÂ∑¶ËæπÁöÑË∑ùÁ¶ª-Áâ©‰ΩìÂ∑¶ËæπÊ°ÜÁõ∏ÂØπ‰∫éÊµèËßàÂô®ÊúÄÂ∑¶ËæπÁöÑË∑ùÁ¶ªÔºåÁ∫µÂêëÂêåÁêÜ
        const leftWidth = e.clientX - this.x;
        const topHeight = e.clientY - this.y;

        document.onmousemove = e => {
          this.completeSelectRegion = false; //ÁßªÂä®Êó∂ÊÑèÂë≥ÁùÄÈáçÊñ∞ÈÄâÂå∫ÔºåÊú™ÂÆåÊàêÈÄâÂå∫ÈöêËóèÂ∑•ÂÖ∑Êù°

          // ÊéßÂà∂ÊãñÊãΩÁâ©‰ΩìÁöÑËåÉÂõ¥Âè™ËÉΩÂú®ÊµèËßàÂô®ËßÜÁ™óÂÜÖÔºå‰∏çÂÖÅËÆ∏Âá∫Áé∞ÊªöÂä®Êù°ÊàñÊãñÂá∫ÂèØËßÜÂå∫Âüü
          let posX = e.clientX - leftWidth;
          let posY = e.clientY - topHeight;

          if (posX < 0) {
            posX = 0;
          } else if (posX > document.body.clientWidth - this.width) {
            posX = document.body.clientWidth - this.width;
          }

          if (posY < 0) {
            posY = 0;
          } else if (posY > document.body.clientHeight - this.height) {
            posY = document.body.clientHeight - this.height;
          }

          //ÊãñÂä®
          this.x = posX;
          this.y = posY;
          this.captureRegionStyle.left = this.x + "px";
          this.captureRegionStyle.top = this.y + "px";
        }

        document.onmouseup = () => {
          // Èº†Ê†áÊä¨Ëµ∑Êó∂‰∏çÂÜçÁßªÂä®
          // È¢ÑÈò≤Èº†Ê†áÂºπËµ∑Êù•ÂêéËøò‰ºöÂæ™ÁéØÔºàÂç≥È¢ÑÈò≤Èº†Ê†áÊîæ‰∏äÂéªÁöÑÊó∂ÂÄôËøò‰ºöÁßªÂä®Ôºâ
          document.onmousemove = null;
          this.completeSelectRegion = true; //ÈáçÊñ∞ÈÄâÂèñÂÆåÊàêÔºåÊòæÁ§∫Â∑•ÂÖ∑Êù°

          //-------ËÆæÁΩÆÂÆΩÈ´ò„ÄÅÊ∏ÖÁ©∫ÁîªÂ∏ÉÈÉΩ‰ºö‰ΩøcanvasÁä∂ÊÄÅÊ∏ÖÁ©∫ÔºåÊâÄ‰ª•Âú®ËøôÈáåÂ§ÑÁêÜÈ´òdpiÊ®°Á≥äÈóÆÈ¢ò
          setCanvas(this.$refs.display,this.canvasWidth,this.canvasHeight)
          setCanvas(this.$refs.assist,this.canvasWidth,this.canvasHeight)
          //------
          this.clipDesktop()
        };
      }
    }
  },
  directives: {
    position: function(el, binding) {
      //bind + update
      let { x, y, width, height } = binding.value;
      const { clientWidth, clientHeight } = document.body;
      const toolbarLength = 555;
      const toolbarHeight = 40;
      const isBeyondLeft = (toolbarLength - width) / 2 > x; //Â∑¶ËæπÁ¢∞Â£Å‰∫Ü
      const isBeyondRight =
        (toolbarLength - width) / 2 > clientWidth - x - width; //Âè≥ËæπÁ¢∞Â£Å‰∫Ü
      const isBeyondBottom =
        toolbarHeight + 20 > clientHeight - y - height; //‰∏ãËæπÁ¢∞Â£Å‰∫Ü

      el.style.position = "absolute";
      el.style.zIndex = 999;
      el.style.left = isBeyondLeft
        ? "10px"
        : isBeyondRight
        ? clientWidth - toolbarLength - 20 + "px"
        : x + width / 2 - toolbarLength / 2 + "px";
      el.style.top = isBeyondBottom
        ? y - 60 + "px"
        : y + height + 40 + "px";
    }
  }
};
</script>

<style lang="less">
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3); //ÂÖ≥ÈîÆÂú®ËøôÈáåÔºåË∞ÉËØïÊó∂ÂèëÁé∞ÊòØ#fff
}
#app,#layout {
  width: 100%;
  height: 100%;
  z-index: 1;
  // background: rgba(0, 0, 0, 0.1);
}
#mask {
  // background: rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 100%;
  z-index: 99;
}

#desktop-canvas,
#video {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -999;
  width: 100%;
  height: 100%;
  pointer-events: none;
  visibility: hidden;
}
</style>
